.about-us-section{
  width: 100%;
  /* padding: calc(10% - 300px) 0px; */
  margin-top: 0px;
}
.about-us-section .img-head{
  position: relative;
  overflow: hidden;
  border-radius: 50px 0px 50px 0px;
  margin: 20px;
}
.about-us-section .img-head img{
  width: 100%;
  transition: transform .3s;
}
.about-us-section .img-head:hover img{
   transform: scale(1.2)
}
.about-us-section .img-head::after{
  content: '';
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right bottom,var(--colorOne),var(--colorOne));
  position: absolute;
  left: 0;
  top: 0;
  opacity: .5;
}
.about-us-section .text-head{
  font-size: 28px;
  color: var(--colorOne);
}
.about-us-section .break-small{
  width: 80px;
  height: 3px;
  background-color: var(--colorOne);
}
.about-us-section .text-para{
  font-size: 16px;
  color: #444;
}
.about-us-section .box{
  background-color: var(--colorOne);
  color: #fff;
  text-align: center;
  padding: 20px 0px;
  position: relative;
  overflow: hidden;
}
.about-us-section .box i{
   font-size: 42px;
}
.about-us-section .box h4{
  font-size: 14px ;
}
.about-us-section .box p{
  font-size: 22px ;
}
.about-us-section .box::after,
.about-us-section .box::before{
  content: "";
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: #fff5;
  border-radius: 50%;
  transition: transform .3s;
}
.about-us-section .box::after{
  left: -50px;
  top: -50px;
}
.about-us-section .box::before{
  right: -50px;
  bottom: -50px;
}
.about-us-section .box:hover::after,
.about-us-section .box:hover:before{
  transform: scale(1.2);
}


@media (min-width: 200px) and (max-width: 992px){

  .about-us-section .img-head{
    display: none;
  }

.about-us-section{
  margin-bottom: 200px;
}
 

}